var enumerateDevices = require('enumerate-devices');

module.exports = function (deviceId) {
  return enumerateDevices().then(function (devices) {
    return devices.filter(function (devices) {
      return devices.kind === 'videoinput';
    });
  }).then(function (cameras) {
    var constraints = {
      audio: false,
      video: true
    };

    if (cameras[deviceId]) {
      constraints['video'] = {
        deviceId: {
          exact: cameras[deviceId].deviceId
        }
      };
    }

    console.log(cameras);
    return window.navigator.mediaDevices.getUserMedia(constraints);
  }).then(function (stream) {
    var video = document.createElement('video'); //  video.src = window.URL.createObjectURL(stream)

    video.srcObject = stream;
    return new Promise(function (resolve, reject) {
      video.addEventListener('loadedmetadata', function () {
        video.play().then(function () {
          return resolve({
            video: video
          });
        });
      });
    });
  }).catch(console.log.bind(console));
};