var transforms = require('./glsl-transforms.js');

var Output = function Output(opts) {
  var _this = this;

  this.regl = opts.regl;
  this.positionBuffer = this.regl.buffer([[-2, 0], [0, -2], [2, 2]]);
  this.clear();
  this.pingPongIndex = 0; // for each output, create two fbos to use for ping ponging

  this.fbos = Array(2).fill().map(function () {
    return _this.regl.framebuffer({
      color: _this.regl.texture({
        width: opts.width,
        height: opts.height,
        format: 'rgba'
      }),
      depthStencil: false
    });
  }); // array containing render passes

  this.passes = []; // console.log("position", this.positionBuffer)
};

Output.prototype.resize = function (width, height) {
  this.fbos.forEach(function (fbo) {
    fbo.resize(width, height);
  });
}; // Object.keys(transforms).forEach((method) => {
//   Output.prototype[method] = function (...args) {
//   //  console.log("applying", method, transforms[method])
//     this.applyTransform(transforms[method], args)
//
//     return this
//   }
// })


Output.prototype.getCurrent = function () {
  // console.log("get current",this.pingPongIndex )
  return this.fbos[this.pingPongIndex];
};

Output.prototype.getTexture = function () {
  //  return this.fbos[!this.pingPongIndex]
  var index = this.pingPongIndex ? 0 : 1; //  console.log("get texture",index)

  return this.fbos[index];
};

Output.prototype.clear = function () {
  this.transformIndex = 0;
  this.fragHeader = "\n  precision highp float;\n\n  uniform float time;\n  varying vec2 uv;\n  ";
  this.fragBody = ""; //
  // uniform vec4 color;
  // void main () {
  //   gl_FragColor = color;
  // }`

  this.vert = "\n  precision highp float;\n  attribute vec2 position;\n  varying vec2 uv;\n\n  void main () {\n    uv = position;\n    gl_Position = vec4(2.0 * position - 1.0, 0, 1);\n  }";
  this.attributes = {
    position: this.positionBuffer
  };
  this.uniforms = {
    time: this.regl.prop('time'),
    resolution: this.regl.prop('resolution') //  this.compileFragShader()

  };
  this.frag = "\n       ".concat(this.fragHeader, "\n\n      void main () {\n        vec4 c = vec4(0, 0, 0, 0);\n        vec2 st = uv;\n        ").concat(this.fragBody, "\n        gl_FragColor = c;\n      }\n  ");
  return this;
}; // Output.prototype.compileFragShader = function () {
//   var frag = `
//     ${this.fragHeader}
//
//     void main () {
//       vec4 c = vec4(0, 0, 0, 0);
//       vec2 st = uv;
//       ${this.fragBody}
//       gl_FragColor = c;
//     }
//   `
// // console.log("FRAG", frag)
//   this.frag = frag
// }


Output.prototype.render = function () {
  var _this2 = this;

  this.draw = this.regl({
    frag: this.frag,
    vert: this.vert,
    attributes: this.attributes,
    uniforms: this.uniforms,
    count: 3,
    framebuffer: function framebuffer() {
      _this2.pingPongIndex = _this2.pingPongIndex ? 0 : 1;
      return _this2.fbos[_this2.pingPongIndex];
    }
  });
};

Output.prototype.renderPasses = function (passes) {
  var _this3 = this;

  var self = this; //  console.log("passes", passes)

  this.passes = passes.map(function (pass, passIndex) {
    //  console.log("get texture",index)
    var uniforms = Object.assign(pass.uniforms, {
      prevBuffer: function prevBuffer() {
        var index = _this3.pingPongIndex ? 0 : 1; //  console.log('pass index', passIndex, 'fbo index', index)

        return _this3.fbos[_this3.pingPongIndex ? 0 : 1];
      }
    });
    return {
      draw: self.regl({
        frag: pass.frag,
        vert: self.vert,
        attributes: self.attributes,
        uniforms: uniforms,
        count: 3,
        framebuffer: function framebuffer() {
          self.pingPongIndex = self.pingPongIndex ? 0 : 1; //  console.log('pass index', passIndex, 'render index',  self.pingPongIndex)

          return self.fbos[self.pingPongIndex];
        }
      })
    };
  });
};

Output.prototype.tick = function (props) {
  //  this.draw(props)
  this.passes.forEach(function (pass) {
    return pass.draw(props);
  });
};

module.exports = Output;